import { mobileEmbedPluginFactory } from '@ali/4ever-factory';
import createRenderNode from './createRenderNode';
import * as actions from '../actions';

export { actions };

const createMobileEmbedPlugin = mobileEmbedPluginFactory({
  createRenderNode,
  configName: 'video',
  actions,
}, () => ({
  key: 'video'
}));

export default createMobileEmbedPlugin;
